/* _______________ Global _______________ */
.page {
  font-size: 10px;
  line-height: 1.5;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.body {
  display: grid;
  color: var(--text-color);
  font-size: 1.5rem;
  background-color: var(--bg-color);
  grid-template-rows: 1fr min-content;
  height: 100%;
  min-height: 100vh;
  overflow-x: hidden;
}

.main {
  margin-top: 150px;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

.container {
  margin: 0 auto;
  max-width: 1450px;
  padding: 0 15px;
}

.container-fluid {
  width: 100vw;
  background-color: #bd98a2;
  padding: 20px;
}

.container--inner {
  padding: 0 20px;
  display: flex;
  flex-direction: column;
}

::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  background-color: var(--bg-color);
  border-radius: 1rem;
}

::-webkit-scrollbar-thumb {
  background-color: var(--accent-color);
  border-radius: 1rem;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #3a4276;
}

.lock {
  overflow: hidden;
}

.text {
  font-weight: 400;
  line-height: 1.7;
  font-size: clamp(1.4rem, 0.722rem + 0.45vw, 1.8rem);
}

.title {
  font-size: 48px;
  line-height: 1.3;
  font-weight: 600;
  color: #ffffff;
}


/* _______________ /Global _______________ */