/* _______________ HEADER _______________ */
.header {
  position: fixed;
  width: 100%;
  box-shadow: 0 0 5px 0 var(--text-color);
  color: inherit;
  background-color: inherit;
  font-size: 1.6rem;
  font-weight: normal;
  z-index: 11;
  padding: 30px 0;

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 15px;
  }

  &__logo {
    &-svg {
      max-width: 150px;
      width: 100%;
      min-width: 80px;
    }

    &:hover {
      opacity: 0.7;
    }
  }

  &__menu {
    display: flex;
    align-items: center;
    column-gap: 40px;
    z-index: 12;
    overflow-y: auto;
    text-transform: capitalize;
  }

  &__menu-item {
    position: relative;
    padding: 5px 0;
    transition: all .3s;

    &:before {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      height: 3px;
      width: 100%;
      background-color: var(--accent-color);
      opacity: 0;
      visibility: hidden;
      transition: opacity .3s;
    }

    @media(any-hover: hover) {
      &:hover {
        color: #ffffff;
        &::before {
          visibility: visible;
          opacity: 1;
        }
      }
    }
  }

  &__buttons {
    display: flex;
    gap: 5px;
  }
  &__buttons-btn {
    padding: 11px 20px;
    color: inherit;
    border: 2px solid var(--accent-color);
    cursor: pointer;
    transition: all .3s;
    @media(any-hover: hover) {
      &:hover {
        color: var(--accent-color);
      }
    }
  }
  &__buttons-btn:nth-of-type(1) {
    background-color: transparent;
  }
  &__buttons-btn:nth-of-type(2) {
    background-color: var(--accent-color);
    @media(any-hover: hover) {
      &:hover {
        color: #ffffff;
      }
    }
  }

  &__menu-overlay {
    display: none;
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, .5);
    opacity: 0.8;
    backdrop-filter: blur(2px);
  }

  &__btn-area {
    padding: 10px;
    margin-right: -10px;
    z-index: 12;
    display: none;
    cursor: pointer;
  }

  &__btn {
    width: 50px;
    height: 24px;
    position: relative;
    transition: all .3s;
  }

  &__btn>span {
    display: block;
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    height: 2px;
    background-color: var(--text-color);
    transition: all .3s;
  }

  &__btn>span:nth-child(2) {
    transform: translateY(-50%);
    top: 50%;

  }

  &__btn>span:nth-child(3) {
    transform: translateY(-100%);
    top: 100%;
  }

  &__btn.show>span:nth-child(2) {
    display: none;
  }

  &__btn.show>span:nth-child(1) {
    transform: translateY(600%) rotate(45deg);
  }

  &__btn.show>span:nth-child(3) {
    transform: translateY(-600%) rotate(-45deg);
  }
}

@media (max-width: 968px) {
  .header.show {
    background-color: var(--bg-color);
    transition: all 0.3s;
  }

  .header {
    padding: 0;
    &__logo {
      padding: 10px 0;
    }

    &__menu {
      flex-direction: column;
      position: absolute;
      width: 40%;
      height: 100vh;
      gap: 0;
      background-color: var(--bg-color);
      top: 0;
      left: 0;
      right: 0;
      transition: all 0.3s;
      transform: translateX(-100%);
    }

    &__menu.show {
      transform: translateX(0%);
      overflow: auto;
    }

    &__menu-overlay.show {
      display: block;
    }

    &__menu-item {
      padding: 25px 0;
      text-align: center;
      position: relative;
      text-transform: uppercase;
    }

    &__btn-area {
      display: block;
    }

    &__btn {
      margin-right: 20px;
    }
    &__buttons-btn:nth-of-type(1) {
      display: none;
    }
  }
}

@media(max-width: 768px) {
  .header {
    &__logo {
      margin-right: 0;
    }
    &__buttons-btn:nth-of-type(2) {
      padding: 5px 10px;
    }
    &__logo {
      &-svg {
        width: 90px;
      }
    }
  }
}

@media(max-width: 600px) {
  .header {
    &__menu {
      width: 60%;
    }
    &__menu.show {
      top: 0;
    }

    &__btn {
      margin-right: 0;
    }
  }
}