/* _______________ Page index _______________ */
.hero {
  margin-bottom: 150px;

  &__title {
    font-size: clamp(3.5rem, 1.991rem + 2.55vw, 6.25rem);
    font-weight: 600;
    line-height: 1.2;
    color: #ffffff;
  }

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__content {
    flex: 0 0 calc(700px / 1420px * 100%);
    padding-bottom: 150px;
  }

  &__filter {
    margin-top: 60px;
    position: absolute;
  }

  &__img {
    flex: 0 0 calc(700px / 1420px * 100%);

    img {
      width: 100%;
    }
  }

  &__deco {
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: -10;
  }
}

.filter {
  height: 100px;
  width: 880px;
  background-color: #ffffff;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 165px;
  color: #121b25;

  &__block {
    &:nth-of-type(2) {
      border-right: 1px solid #E8E8E8;
      border-left: 1px solid #E8E8E8;
    }
  }

  .block-filter {
    position: relative;

    &__button {
      padding-left: 40px;
      display: flex;
      justify-content: center;
      height: 100%;
      flex-direction: column;
    }

    &__header {
      display: flex;
    }

    &__type {
      padding-right: 2px;
      font-size: 16px;
      cursor: pointer;
    }

    &__icon {
      cursor: pointer;
      transition: all .3s;

      svg {
        width: 25px;
      }

      &.active {
        transform: rotate(180deg);
      }
    }

    &__value {
      color: var(--bg-color);
      font-size: 18px;
      font-weight: 600;
      line-height: 1.2;
    }

    &__dropdown {
      position: absolute;
      top: 100%;
      left: 0;
      right: 0;
      background-color: #ffffff;
      border-radius: 5px;
      overflow: hidden;
      opacity: 0;
      visibility: hidden;
      pointer-events: none;
      transition: all .3s;

      &.active {
        opacity: 1;
        visibility: visible;
        pointer-events: all;
        transform: translateY(5px);
      }
    }

    &__item {
      padding: 10px;
      transition: background .2s;
      text-transform: capitalize;
      cursor: pointer;

      @media (any-hover: hover) {
        &:hover {
          background-color: #E8E8E8;
        }
      }
    }
  }

  &__btn {
    padding: 5px;

    button {
      background-color: var(--accent-color);
      border: none;
      height: 100%;
      width: 100%;
      font-size: 24px;
      line-height: 28px;
      font-weight: 500;
      color: #d6d6d6;
      cursor: pointer;

      &:hover {
        color: #ffffff;
      }

      &:active {
        box-shadow: inset 0 0 5px 0 black;
      }
    }
  }
}

.featured {
  margin-bottom: 180px;

  &__title {
    margin-bottom: 50px;
  }

  &__box {
    display: flex;
    max-width: 100%;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    flex-wrap: wrap;
  }

  &__item {
    flex: 0 0 auto;
  }
}

.popular {
  width: 100vw;
  background: #1A242F url('../img/slider-bg.webp');
  padding: 140px 0;
  margin-bottom: 180px;

  &__title {
    margin-bottom: 30px;
    color: #ffffff;
  }

  &__photo {
    max-width: 100%;
    width: 100%;

    img {
      width: 100%;
      max-width: 460px;
      max-height: 353px;
    }
  }

  &__name {
    font-weight: 600;
    line-height: 1.26;
    font-size: 2.6rem;
    margin-bottom: 2px;
    color: #ffffff;
  }

  &__adress {
    font-weight: 400;
    line-height: 1.7;
    font-size: 1.8rem;
    font-style: normal;
    color: var(--text-color);
    margin-bottom: 20px;
  }

  &__options {
    display: flex;
    align-items: center;
    gap: 20px;
  }

  &__option {
    display: flex;
    align-items: center;
    gap: 5px;
  }

  &__option-text {
    color: #ffffff;
    font-size: 1.6rem;
    line-height: 1.9;
    font-weight: 400;
  }

  &__btn {
    padding: 21px 42px;
    background-color: var(--accent-color);
    font-size: 24px;
    line-height: 28px;
    font-weight: 500;
    text-transform: capitalize;
    color: #d6d6d6;
    display: block;
    margin: 60px auto 0 auto;
    border: none;
    cursor: pointer;

    &:hover {
      color: #ffffff;
    }

    &:active {
      box-shadow: inset 0 0 5px 0 black;
    }
  }
}

.choose {
