    }
  }

  &__item-title {
    font-size: 26px;
    line-height: 1.3;
    font-weight: 600;
    text-transform: capitalize;
    color: #ffffff;
    margin-bottom: 10px;
  }

  &__item-text {
    font-weight: 400;
    font-size: 18px;
    line-height: 1.7;

  }

  .slider {

    &__item {
      flex: 0 0 100%;
      max-width: 100%;
    }

    &__control {
      display: none;
    }

    // &__control {
    //   top: 100%;

    //   height: 40px;
    //   width: 40px;
    // }
    // &__control[data-slide=next] {
    //   right: 100px;
    //   transform: translateX(-50%);
    // }
    // &__control[data-slide=prev] {
    //   left: 100px;
    //   transform: translateX(50%);
    // }

  }

  // Comment
  &__comment {
    max-width: 1020px;
    padding: 10px;
  }

  &__comment-quotes {
    margin-top: 60px;
    font-size: 200px;
    line-height: 0.1;
    color: var(--accent-color);
  }

  &__comment-text {
    font-size: 32px;
    line-height: 1.5;
    font-weight: 400;
    color: #ffffff;
    margin-bottom: 40px;
  }

  &__comment-name {
    color: #ffffff;
    font-weight: 600;
    line-height: 1.3;
    font-size: 22px;
  }

  &__comment-prof {
    font-size: 18px;
    line-height: 1.7;
  }

  &__deco {
    position: absolute;
    left: 0;
    bottom: -15%;
    z-index: -10;
    height: auto;
  }
}

.help {
  background: linear-gradient(270deg, #1A242F 0.01%, rgba(26, 36, 47, 0) 82.97%);
  padding: 160px 0;
  width: 100vw;
  position: relative;

  &__container {
    display: flex;
    gap: 50px;
  }

  &__photo {
    display: flex;
    gap: 4%;
    width: 50%;
  }

  &__img {
    max-height: 551px;
    object-fit: cover;
    width: 48%;
  }

  &__content {
    width: 50%;
    align-self: center;
    z-index: 1;
  }

  &__title {
    margin-bottom: 22px;
  }

  &__text {
    margin-bottom: 45px;
    font-size: 18px;
    line-height: 1.7;
    font-weight: 400;
  }

  &__btn {
    padding: 21px 42px;
    background-color: var(--accent-color);
    font-size: 24px;
    line-height: 28px;
    font-weight: 500;
    text-transform: capitalize;
    color: #d6d6d6;
    display: block;
    text-align: center;
    max-width: 250px;
    border: none;
    cursor: pointer;

    &:hover {
      color: #ffffff;
    }

    &:active {
      box-shadow: inset 0 0 5px 0 black;
    }
  }

  &__deco {
    position: absolute;
    bottom: -10%;
    z-index: 0;
    right: 0;
  }
}

.begin {
  padding: 180px 0;
  position: relative;
  // overflow-y: hidden;

  &__container {
    display: flex;
    gap: 2%;
    align-items: center;
  }

  &__title {
    font-size: 68px;
    font-weight: 600;
    line-height: 1.2;
    color: #ffffff;
    text-align: left;
    text-transform: capitalize;
    width: 40%;
  }

  &__content {
    width: 58%;
    font-size: 18px;
    line-height: 1.7;
  }

  &__text {
    margin-bottom: 30px;
  }

  &__btn {
    background-color: var(--accent-color);
    border: none;
    padding: 22px 42px;
    font-size: 24px;
    line-height: 28px;
    font-weight: 500;
    color: #d6d6d6;
    cursor: pointer;

    &:hover {
      color: #ffffff;
    }

    &:active {
      box-shadow: inset 0 0 5px 0 black;
    }
  }
  &__deco {
    position: absolute;
    bottom: -60%;
    z-index: -2;
  }
}

