/* _______________ FOOTER _______________ */
.footer {
  background-color: #1A242F;
  font-size: 18px;
  &__container {
    padding: 140px 20px 95px 20px;
    display: flex;
    gap: 100px;
    align-items: center;
  }

  &__logo {
    &:hover {
      opacity: 0.7;
    }
  }

  &__social {
    display: flex;
    flex-direction: column;
    max-width: 340px;
    gap: 20px;
  }

  &__social-text {
    margin-bottom: 10px;
  }

  &__social-links {
    display: flex;
    gap: 10px;
  }

  &__info {
    display: flex;
    gap: 10px;
    justify-content: space-between;
    flex-basis: 100%;
  }

  &__info-item-title {
    font-size: 26px;
    line-height: 1.2;
    font-weight: 600;
    margin-bottom: 15px;
  }

  &__info-item-list-link {
    margin-bottom: 10px;
    a:hover {
      text-decoration: underline;
    }
  }
  &__copy {
    padding: 20px 0;
    box-shadow: 0 0 2px 0;
  }
  &__copy-container {
    display: flex;
    gap: 20px;
    justify-content: space-between;
    align-items: center;
  }
  &__copy-author {
    color: var(--accent-color);
    &:hover {
      text-decoration: underline;
    }
  }
}

@media(max-width: 992px) {
  .footer {
    &__container {
      gap: 20px;
    }
    &__social {
      align-self: flex-start;
    }
    &__info {
      flex-wrap: wrap;
      column-gap: 15px;
      row-gap: 40px;
    }
    &__info-item {
      min-width: 127px;
    }
  }
}

@media(max-width: 768px) {
  .footer {
    font-size: 16px;
    line-height: 1.5;
    &__container {
      padding: 70px 15px 65px 15px;
    }
  }
}
@media(max-width: 650px) {
  .footer {
    &__container {
      flex-direction: column;
      gap: 50px;
    }
    &__social {
      max-width: 100%;
      width: 100%;
      display: flex;
      align-items: center;
      gap: 10px;
    }
    &__social-links {
      display: flex;
      width: 100%;
      justify-content: space-between;
    }
    &__copy-author {
      text-align: center;
    }
  }
}