/* _______________ Page index adaptive _______________ */
@media(max-width: 1530px) {
  .popular {
    .slider {
      &__control {
        display: none;
      }
    }
  }

  .choose {
    &__deco {
      width: 300px;
    }
  }

}

@media (max-width: 1450px) {
  .hero__container {
    flex-direction: row;
  }

  .hero__filter {
    position: static;
    width: 100%;
    grid-template-rows: 80px 80px;
    grid-template-columns: 1fr 1fr;
    gap: 5px;
    background-color: transparent;
  }

  .block-filter {
    background-color: #ffffff;
  }

  .filter__btn {
    padding: 0px;
  }

  .filter .block-filter__dropdown {
    z-index: 2;
    background-color: var(--bg-color);
    color: var(--text-color);
    box-shadow: 0 0 2px 0;
  }

  .block-filter__dropdown {
    .block-filter__item:hover {
      background-color: var(--accent-color);
    }
  }

  .filter {
    height: auto;
  }
}

@media (max-width: 992px) {
  .title {
    font-size: 36px;
  }

  .hero__filter {
    grid-template-rows: repeat(4, 60px);
    grid-template-columns: 1fr;
    gap: 10px;
  }

  .hero {
    margin-bottom: 40px
  }

  .featured {
    &__box {
      justify-content: flex-start;
    }
  }

  .popular {
    .slider {
      &__item {
        flex: 0 0 50%;
        max-width: 50%;
      }
    }

    &__options {
      flex-wrap: wrap;
    }

  }

  .choose {
    &__deco {
      width: 250px;
    }
  }

  .begin {
    &__title {
      font-size: 58px;
    }
    &__deco {
      height: 500px;
    }
  }
}

@media (max-width: 768px) {
  .title {
    text-align: center;
  }

  .hero__container {
    flex-direction: column;
  }

  .hero__content {
    padding-bottom: 30px;
  }

  .hero__filter {
    margin-top: 30px;
  }

  .featured {
    margin-bottom: 100px;
  }

  .popular {
    padding: 70px 0;
    margin-bottom: 100px;

    .slider {
      &__item {
        flex: 0 0 100%;
        max-width: 100%;
      }
    }

    &__photo {
      img {
        max-width: 100%;
      }
    }

    &__name {
      font-size: 2.2rem;
    }

    &__adress {
      font-size: 1.6rem;
    }

    &__options {
      gap: 10px;
    }

    &__option-text {
      font-size: 1.4rem;
    }

    &__btn {
      width: 100%;
    }
  }

  .choose {
    margin-bottom: 50px;

    &__list {
      flex-direction: column;
      align-items: center;
    }

    // &__item-icon {
    //   max-width: 100px;
    // }
    &__item-title {
      font-size: 22px;
    }

    &__item-text {
      font-size: 16px;
      line-height: 1.5;
    }

    &__comment-quotes {
      text-align: center;
    }

    &__comment-text {
      font-size: 18px;
      margin-bottom: 20px;
    }

    &__comment-name {
      font-size: 18px;
    }

    &__comment-prof {
      font-size: 14px;
    }


    &__deco {
      width: 180px;
      bottom: 0;
    }

  }
  .help {
    padding: 70px 0;
    &__container {
      flex-direction: column;
      gap: 30px;
    }
    &__photo {
      width: 100%;
      max-height: 400px;
    }
    &__content {
      width: 100%;
    }
    &__title {
      margin-bottom: 15px;
    }
    &__text {
      font-size: 16px;
      line-height: 1.5;
      margin-bottom: 35px;
    }
    &__btn {
      max-width: 100%;
      width: 100%;
    }
    &__deco {
      width: 35px;
    }
  }
  .begin {
    padding: 70px 0 40px 0;
    &__container {
      flex-direction: column;
    }
    &__title {
      width: 100%;
      font-size: 40px;
      text-align: center;
      margin-bottom: 15px;
    }
    &__content {
      width: 100%;
    }
    &__text {
      font-size: 16px;
      line-height: 1.5;
      margin-bottom: 35px;
    }
    &__btn {
      width: 100%;
    }
    &__deco {
      width: 50%;
      height: 500px;
    }
  }

}

@media (max-width: 375px) {
  .title {
    font-size: 32px;
  }

  .featured {
    &__box {
      justify-content: center;
    }
  }

  .choose {
    &__list {
      gap: 20px;
    }

    &__item-icon {
      max-width: 120px;
      margin-bottom: 10px;
    }

    &__comment {
      text-align: center;
    }
  }
}

/* _______________ /Page index adaptive _______________ */