@charset "UTF-8";
/* _______________ FONTS _______________ */
@import url("https://fonts.googleapis.com/css?family=Work+Sans:regular,500,600&display=swap");
.body {
  font-family: "Work Sans", sans-serif;
  font-size: 1.3rem;
}

/* _______________ VARIABLES _______________ */
/* _______________ Global colors _______________ */
:root {
  --bg-color: #121B25;
  --accent-color: #1AA090;
  --text-color: #D6D6D6;
  --text-color-bold: #ffffff;
  --hover: #ddd444;
}

/* _______________ Global _______________ */
.page {
  font-size: 10px;
  line-height: 1.5;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.body {
  display: grid;
  color: var(--text-color);
  font-size: 1.5rem;
  background-color: var(--bg-color);
  grid-template-rows: 1fr min-content;
  height: 100%;
  min-height: 100vh;
  overflow-x: hidden;
}

.main {
  margin-top: 150px;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

.container {
  margin: 0 auto;
  max-width: 1450px;
  padding: 0 15px;
}

.container-fluid {
  width: 100vw;
  background-color: #bd98a2;
  padding: 20px;
}

.container--inner {
  padding: 0 20px;
  display: flex;
  flex-direction: column;
}

::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  background-color: var(--bg-color);
  border-radius: 1rem;
}

::-webkit-scrollbar-thumb {
  background-color: var(--accent-color);
  border-radius: 1rem;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #3a4276;
}

.lock {
  overflow: hidden;
}

.text {
  font-weight: 400;
  line-height: 1.7;
  font-size: clamp(1.4rem, 0.722rem + 0.45vw, 1.8rem);
}

.title {
  font-size: 48px;
  line-height: 1.3;
  font-weight: 600;
  color: #ffffff;
}

/* _______________ /Global _______________ */
/* _______________ HEADER _______________ */
.header {
  position: fixed;
  width: 100%;
  box-shadow: 0 0 5px 0 var(--text-color);
  color: inherit;
  background-color: inherit;
  font-size: 1.6rem;
  font-weight: normal;
  z-index: 11;
  padding: 30px 0;
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 15px;
}
.header__logo-svg {
  max-width: 150px;
  width: 100%;
  min-width: 80px;
}
.header__logo:hover {
  opacity: 0.7;
}
.header__menu {
  display: flex;
  align-items: center;
  column-gap: 40px;
  z-index: 12;
  overflow-y: auto;
  text-transform: capitalize;
}
.header__menu-item {
  position: relative;
  padding: 5px 0;
  transition: all 0.3s;
}
.header__menu-item:before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 3px;
  width: 100%;
  background-color: var(--accent-color);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s;
}
@media (any-hover: hover) {
  .header__menu-item:hover {
    color: #ffffff;
  }
  .header__menu-item:hover::before {
    visibility: visible;
    opacity: 1;
  }
}
.header__buttons {
  display: flex;
  gap: 5px;
}
.header__buttons-btn {
  padding: 11px 20px;
  color: inherit;
  border: 2px solid var(--accent-color);
  cursor: pointer;
  transition: all 0.3s;
}
@media (any-hover: hover) {
  .header__buttons-btn:hover {
    color: var(--accent-color);
  }
}
.header__buttons-btn:nth-of-type(1) {
  background-color: transparent;
}
.header__buttons-btn:nth-of-type(2) {
  background-color: var(--accent-color);
}
@media (any-hover: hover) {
  .header__buttons-btn:nth-of-type(2):hover {
    color: #ffffff;
  }
}
.header__menu-overlay {
  display: none;
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0.8;
  backdrop-filter: blur(2px);
}
.header__btn-area {
  padding: 10px;
  margin-right: -10px;
  z-index: 12;
  display: none;
  cursor: pointer;
}
.header__btn {
  width: 50px;
  height: 24px;
  position: relative;
  transition: all 0.3s;
}
.header__btn > span {
  display: block;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background-color: var(--text-color);
  transition: all 0.3s;
}
.header__btn > span:nth-child(2) {
  transform: translateY(-50%);
  top: 50%;
}
.header__btn > span:nth-child(3) {
  transform: translateY(-100%);
  top: 100%;
}
.header__btn.show > span:nth-child(2) {
  display: none;
}
.header__btn.show > span:nth-child(1) {
  transform: translateY(600%) rotate(45deg);
}
.header__btn.show > span:nth-child(3) {
  transform: translateY(-600%) rotate(-45deg);
}

@media (max-width: 968px) {
  .header.show {
    background-color: var(--bg-color);
    transition: all 0.3s;
  }
  .header {
    padding: 0;
  }
  .header__logo {
    padding: 10px 0;
  }
  .header__menu {
    flex-direction: column;
    position: absolute;
    width: 40%;
    height: 100vh;
    gap: 0;
    background-color: var(--bg-color);
    top: 0;
    left: 0;
    right: 0;
    transition: all 0.3s;
    transform: translateX(-100%);
  }
  .header__menu.show {
    transform: translateX(0%);
    overflow: auto;
  }
  .header__menu-overlay.show {
    display: block;
  }
  .header__menu-item {
    padding: 25px 0;
    text-align: center;
    position: relative;
    text-transform: uppercase;
  }
  .header__btn-area {
    display: block;
  }
  .header__btn {
    margin-right: 20px;
  }
  .header__buttons-btn:nth-of-type(1) {
    display: none;
  }
}
@media (max-width: 768px) {
  .header__logo {
    margin-right: 0;
  }
  .header__buttons-btn:nth-of-type(2) {
    padding: 5px 10px;
  }
  .header__logo-svg {
    width: 90px;
  }
}
@media (max-width: 600px) {
  .header__menu {
    width: 60%;
  }
  .header__menu.show {
    top: 0;
  }
  .header__btn {
    margin-right: 0;
  }
}
/* _______________ FOOTER _______________ */
.footer {
