  max-width: 340px;
  gap: 20px;
}
.footer__social-text {
  margin-bottom: 10px;
}
.footer__social-links {
  display: flex;
  gap: 10px;
}
.footer__info {
  display: flex;
  gap: 10px;
  justify-content: space-between;
  flex-basis: 100%;
}
.footer__info-item-title {
  font-size: 26px;
  line-height: 1.2;
  font-weight: 600;
