  }
  .footer__info {
    flex-wrap: wrap;
    column-gap: 15px;
    row-gap: 40px;
  }
  .footer__info-item {
    min-width: 127px;
  }
}
@media (max-width: 768px) {
  .footer {
    font-size: 16px;
    line-height: 1.5;
  }
  .footer__container {
    padding: 70px 15px 65px 15px;
  }
}
@media (max-width: 650px) {
  .footer__container {
    flex-direction: column;
    gap: 50px;
  }
  .footer__social {
    max-width: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    gap: 10px;
  }
  .footer__social-links {
    display: flex;
    width: 100%;
    justify-content: space-between;
  }
  .footer__copy-author {
    text-align: center;
  }
}
/* _______________ Page index _______________ */
.hero {
  margin-bottom: 150px;
}
.hero__title {
  font-size: clamp(3.5rem, 1.991rem + 2.55vw, 6.25rem);
  font-weight: 600;
  line-height: 1.2;
  color: #ffffff;
}
.hero__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.hero__content {
  flex: 0 0 49.2957746479%;
  padding-bottom: 150px;
}
.hero__filter {
  margin-top: 60px;
  position: absolute;
}
.hero__img {
  flex: 0 0 49.2957746479%;
}
.hero__img img {
  width: 100%;
}
.hero__deco {
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: -10;
}

.filter {
  height: 100px;
  width: 880px;
  background-color: #ffffff;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 165px;
  color: #121b25;
}
.filter__block:nth-of-type(2) {
  border-right: 1px solid #E8E8E8;
  border-left: 1px solid #E8E8E8;
}
.filter .block-filter {
  position: relative;
}
.filter .block-filter__button {
  padding-left: 40px;
  display: flex;
  justify-content: center;
  height: 100%;
  flex-direction: column;
}
.filter .block-filter__header {
  display: flex;
}
.filter .block-filter__type {
  padding-right: 2px;
  font-size: 16px;
  cursor: pointer;
}
.filter .block-filter__icon {
  cursor: pointer;
  transition: all 0.3s;
}
.filter .block-filter__icon svg {
  width: 25px;
}
.filter .block-filter__icon.active {
  transform: rotate(180deg);
}
.filter .block-filter__value {
  color: var(--bg-color);
  font-size: 18px;
  font-weight: 600;
  line-height: 1.2;
}
.filter .block-filter__dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background-color: #ffffff;
  border-radius: 5px;
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: all 0.3s;
}
.filter .block-filter__dropdown.active {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  transform: translateY(5px);
}
.filter .block-filter__item {
  padding: 10px;
  transition: background 0.2s;
  text-transform: capitalize;
  cursor: pointer;
}
@media (any-hover: hover) {
  .filter .block-filter__item:hover {
    background-color: #E8E8E8;
  }
}
.filter__btn {
  padding: 5px;
}
.filter__btn button {
  background-color: var(--accent-color);
  border: none;
  height: 100%;
  width: 100%;
  font-size: 24px;
  line-height: 28px;
  font-weight: 500;
  color: #d6d6d6;
  cursor: pointer;
}
.filter__btn button:hover {
  color: #ffffff;
}
.filter__btn button:active {
  box-shadow: inset 0 0 5px 0 black;
}

