/* _______________ /Page index adaptive _______________ */
.slider {
  position: relative;
}

.slider__container {
  overflow: hidden;
}

.slider__wrapper {
  overflow: hidden;
}

.slider__items {
  display: flex;
  transition: transform 0.5s ease;
}

.slider_disable-transition {
  transition: none;
}

.slider__item {
  flex: 1 0 33.3333%;
  max-width: 33.3333%;
  padding: 10px 10px;
  max-height: 500px;
  overflow-y: auto;
  user-select: none;
}

/* кнопки влево и вправо */
.slider__control {
  position: absolute;
  top: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  color: #fff;
  text-align: center;
  background-color: transparent;
  border: 1px solid var(--text-color);
  transform: translateY(-50%);
  cursor: pointer;
  border-radius: 50%;
}

.slider__control_hide {
  display: none;
}

.slider__control[data-slide=prev] {
  left: -50px;
}

.slider__control[data-slide=next] {
  right: -50px;
}

.slider__control:hover,
.slider__control:focus {
  color: #fff;
  text-decoration: none;
  outline: 0;
}

.slider__control:active {
  border-color: var(--accent-color);
}

.slider__control::before {
  content: "";
  display: inline-block;
  width: 10px;
  height: 30px;
  background: transparent no-repeat center center;
  background-size: 100% 100%;
}

.slider__control[data-slide=prev]::before {
  background-image: url(../img/fresh_svg/arrow-left.svg);
}

.slider__control[data-slide=next]::before {
  background-image: url(../img/fresh_svg/arrow-right.svg);
}

/* индикаторы */
.slider__indicators {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 15;
  display: flex;
  justify-content: center;
  margin-top: 0;
  margin-right: 15%;
  margin-bottom: 0;
  margin-left: 15%;
  padding-left: 0;
  list-style: none;
}

.slider__indicators li {
  flex: 0 1 auto;
  box-sizing: content-box;
  width: 30px;
  height: 5px;
  margin-right: 3px;
  margin-left: 3px;
  text-indent: -999px;
  background-color: rgba(255, 255, 255, 0.5);
  background-clip: padding-box;
  border-top: 15px solid transparent;
  border-bottom: 15px solid transparent;
  cursor: pointer;
}

.slider__indicators li.active {
  background-color: rgba(255, 255, 255, 0.9);
}

/*_______________ М о и   с т и л и _______________*/
/* .slider__wrapper – используется в качестве обёртки; */
/* .slider__control – кнопки, с помощью которых мы можем перейти соответственно к предыдущему и следующему слайду. */
/*
В HTML: 
autoplay – включает автоматическую смену слайдов (по умолчанию false);
interval – определяет интервал в миллисекундах между автоматической сменой слайдов (по умолчанию 5000);
loop – указывает нужно ли зацикливать или нет показ слайдов (по умолчанию true);
refresh – включает перерасчет размеров слайдера при изменении ширины HTML документа (по умолчанию true);
swipe – разрешает листать слайды свайпом и перемещением курсора мыши при зажатой левой кнопки мыши (по умолчанию true). 
*/
